import React, { useState } from 'react'
import { BiImageAlt } from 'react-icons/bi';
import { Link } from 'react-router-dom';

import Box from '../../components/Box';
import Flex from '../../components/Flex';
import Image from '../../components/Image';
import Button from '../../components/Button';
import useResponsive from '../../contexts/mediaQuery/useResponsive'
import { responsive } from '../../components/ThemeProvider/theme';

import Filter from '../Filter'
import title from './title.png'
// import SearchContext from '../searchContext'

import Menu from './Menu'
import MountainList from './MountainList';
import Search from './Search';

const Header = ({ siteTitle, ...props }) => {
  const [open, openList] = useState()
  const { isMobile } = useResponsive()
  // console.log(search)
  return (
    <Box
      position="fixed"
      top={0}
      left={0}
      right={0}
      zOrder={2}
      {...props}
    >
      <Flex
        bg="white"
        position="relative"
        alignItems="center"
        boxShadow="0px 1px 0.5px lightgray"
      >
        <Menu openList={openList} />
        <Button.Transparent onClick={() => openList(!open)} mx={responsive('0.5em', '1em')} leftIcon={BiImageAlt}>
          {!isMobile && '選山列表'}
        </Button.Transparent>
        <Search isMobile={isMobile} />
        <Box width={responsive('8em', '10em')} as={Link} to="/" position="absolute" top="50%" left="50%" transform="translate(-50%, -50%)">
          <Image src={title} />
        </Box>
      </Flex>
      <Filter />
      {open && <MountainList />}
    </Box>
  )
}

export default Header
